import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { LoginComponent } from './login/login.component';
import { StudentOnboardFormComponent } from './students/components/student-onboard-form/student-onboard-form.component';
import { StudentListComponent } from './students/components/student-list/student-list.component';
import { PageNotFoundComponent } from './shared/components/page-not-found/page-not-found.component';
import {AuthGuard } from './shared/auth/auth.guard';
import { CanDeactivateGuard } from './shared/auth/can-deactivate.guard';

const routes: Routes = [
  {
    path: 'adminLogin',
    component: LoginComponent
  },
  {
    path: 'studentOnboardForm',
    canActivate : [AuthGuard],
    component: StudentOnboardFormComponent,
    children: [
      {
        path: '',
        component: StudentOnboardFormComponent
      },
      {
        path: 'edit',
        component: StudentOnboardFormComponent
      },
      {
        path: 'view',
        component: StudentOnboardFormComponent
      }
    ]
  },
  {
    path: 'studentList',
    canActivate : [AuthGuard],
    component: StudentListComponent
  },
  {
    path: '',
    redirectTo: 'adminLogin',
    pathMatch: 'full'
  },
  {
    path: '**',
    component: PageNotFoundComponent
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
