import { Component, OnInit, OnDestroy } from '@angular/core';
import Swal from 'sweetalert2';

import { Student } from '../../models/Student';
import { DatasourceService } from '../../../shared/services/datasource.service';

@Component({
  selector: 'app-student-list',
  templateUrl: './student-list.component.html',
  styleUrls: ['./student-list.component.css']
})
export class StudentListComponent implements OnInit {

  studentName:string;
  studentList: Array<Student>;
  filteredList: Array<Student>;

  constructor(private readonly datasource: DatasourceService) { }

  ngOnInit() {
    this.datasource.getStudentList().subscribe((value) => {
      if (value) {
        this.studentList = value;
        this.filteredList = value;
      }
    })
  }

  filteredResult(event) {
    this.filteredResult = event.value;
  }

  deleteStudentRecord(student: Student) {
    const index = this.studentList.indexOf(student);
    Swal.fire({
      title: 'Are you sure you want to delete?',
      type: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes'
    }).then((result) => {
      if (result.value) {
        this.studentList.splice(index, 1);
        this.datasource.setStudentList(this.studentList);
        Swal.fire(
          'Deleted!',
          'Student record has been deleted.',
          'success'
        )
      }
    })
  }

  saveStudent(student: Student) {
    console.log('student going to save : ' + JSON.stringify(student))
    this.datasource.setStudent(student);
  }

}
