import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

import { Category } from '../models/CommonModels';

@Injectable({
  providedIn: 'root'
})
export class DocumentService {
  documentType: string;
  constructor(private http: HttpClient) { }

  fetchDocuments(typeOfDocuments: string): Observable<any> {
    if (typeOfDocuments === Category.Domestic) {
      this.documentType = "DomesticDocuments.json";
    } else if (typeOfDocuments === Category.International) {
      this.documentType = "InternationalDocuments.json";
    }
    return this.http.get('../../../assets/config-files/' + this.documentType);
  }

  fetchAllDocuments(): Observable<any> {
    return this.http.get('../../../assets/config-files/' + 'InternationalDocuments.json');
  }

}
