import { Component, OnDestroy, OnInit } from '@angular/core';
import { DatasourceService } from './shared/services/datasource.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit, OnDestroy {
  appName = 'On Boarding App';

  constructor(private readonly datasourceService: DatasourceService) { }

  ngOnInit() {
    if (JSON.parse(localStorage.getItem('studentList'))) {
      this.datasourceService.setStudentList(JSON.parse(localStorage.getItem('studentList')));
    }
  }

  ngOnDestroy() {
    console.log('CLEANUP CALLED')
    localStorage.removeItem('adminUser');
  }
}
