import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { Student } from 'src/app/students/models/Student';
import { AdminUser } from '../models/AdminUser';

@Injectable({
  providedIn: 'root'
})
export class DatasourceService {

  adminUser = new BehaviorSubject<AdminUser>(new AdminUser());
  savedStudent = new BehaviorSubject<Student>(null);
  studentList = new BehaviorSubject<Array<Student>>(new Array<Student>());

  constructor() { }

  getAdminUser(): Observable<AdminUser> {
    return this.adminUser.asObservable();
  }

  saveAdminUser(adminUser: AdminUser) {
    localStorage.setItem('adminUser', JSON.stringify(adminUser));
    this.adminUser.next(adminUser);
  }

  getStudent(): Observable<Student> {
    return this.savedStudent.asObservable();
  }

  setStudent(student: Student) {
    this.savedStudent.next(student);
  }

  getStudentList(): Observable<Array<Student>> {
    return this.studentList.asObservable();
  }

  setStudentList(studentList: Array<Student>) {
    localStorage.setItem('studentList', JSON.stringify(studentList))
    this.studentList.next(studentList);
  }

}
