import { Pipe, PipeTransform } from '@angular/core';
import { Student } from 'src/app/students/models/Student';

@Pipe({
  name: 'searchStudent'
})
export class SearchStudentPipe implements PipeTransform {

  transform(studentList: Array<Student>, studentName?: string): any {
    console.log('student list in pie : ' + studentList)
    console.log('student name : ' + studentName)
    if (studentName) {
      let filteredList: Array<Student> = new Array<Student>();
      for (let student of studentList) {
        if (student.name.toLowerCase().includes(studentName)) {
          filteredList.push(student);
        }
      }
      return filteredList;
    }
    return studentList;
    //this.searchedStudentList.emit(filteredList);

  }

}
